.tag-button {
  font-size: 12px;
  line-height: 30px;
  display: inline-block;
  height: 30px;
  margin: 5px 3px;
  padding: 0 12px;
  color: @gray-light;
  border-radius: 15px;
  background: @bg-gray;
  &:hover {
    color: @gray;
    background: @bg-gray-hover;
  }
}
.post-page {
  padding: 10px;
  position: relative;
  margin: 0 auto;
  max-width: @post-width;
  @media screen and (min-width: @sm-width) {
    padding: 10px 50px;
  }
  @media screen and (min-width: @post-width) {
    border: 1px solid #ccc;
    border-top: none;
    box-shadow: 0 2px 3px rgba(32, 32, 32, .05);
  }
}